import React from 'react';
import connect from '@vkontakte/vkui-connect';
import { View } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import Items from './panels/Items';
import CreateItem from './panels/CreateItem';

class App extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
			activePanel: 'home',
			fetchedUser: null,
			items: [
				{ id: 1, title: "Первый элемент", description: '', numberMembers: 0 },
				{ id: 2, title: "Второй элемент", description: '', numberMembers: 0 },
				{ id: 3, title: "Третий элемент", description: '', numberMembers: 0 },
			]
		};
	}

	componentDidMount() {
		connect.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
					this.setState({ fetchedUser: e.detail.data });
					break;
				default:
					console.log(e.detail.type);
			}
		});
		connect.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};

	createItem = (e) => {
		console.log("createItem");
		const newItems = this.state.items.slice();
		newItems.push({ 
			title: e.title,
			description: e.description,
			numberMembers: e.numberMembers
		 });
		this.setState({ 
			items: newItems,
			activePanel: "items",
		});
	}

	render() {
		return (
			<View activePanel={this.state.activePanel}>
				<Home id="home" fetchedUser={this.state.fetchedUser} go={this.go} />
				<Persik id="persik" go={this.go} />
				<Items id="items" go={this.go} items={this.state.items} />
				<CreateItem id="createItem" go={this.go} createItem={this.createItem} />
			</View>
		);
	}
}

export default App;



