import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, List, Group, Button, Cell, Div, FormLayout, Input, Textarea} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

class CreateItem extends React.Component {
	constructor(props) {
		super(props);

		this.state = {
            title: '',
            description: '',
            numberMembers: 0
		};
    }  
    
    writeTitle(e) {
        this.setState({
            title: e.target.value
        });
    }

    writeDescription(e) {
        this.setState({
            description: e.target.value
        });
    }

    writeNumberMembers(e) {
        this.setState({
            numberMembers: e.target.value
        });
    }
    
    render() {
        return (
            <Panel id={this.props.id}>
                <PanelHeader
                    left={<HeaderButton onClick={this.props.go} data-to="items">
                        {osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
                    </HeaderButton>}
                >
                    Добавление записи
                </PanelHeader>
    
                <FormLayout>
                    <Input top="Заголовок" onChange={(e) => { this.writeTitle(e) }} />
                    <Textarea top="Описание" onChange={(e) => { this.writeDescription(e) }} />
                    <Input top="Максимальное количество участников" onChange={(e) => { this.writeNumberMembers(e) }} />
                    <Button size="xl" onClick={() => this.props.createItem(this.state)}>Добавить</Button>
                </FormLayout>        
            </Panel>
        );
    }    
}

CreateItem.propTypes = {
	id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default CreateItem;


