import React from 'react';
import PropTypes from 'prop-types';
import {Panel, PanelHeader, HeaderButton, platform, IOS, List, Group, Button, Cell, Div} from '@vkontakte/vkui';
import persik from '../img/persik.png';
import './Persik.css';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Items = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			Список элементов
		</PanelHeader>

        <Group>
            <Button size="l" onClick={props.go} data-to="createItem">
				Добавить запись
			</Button>         
        </Group>

        <Group>
            <List>
                {props.items.map((item) =>
                    <Cell key={item.id} asideContent={
                        <Div>
                            <Button size="l">Изменить</Button>
                            <Button size="l">Удалить</Button>
                        </Div>
                    }>{item.title}</Cell>
                )}
            </List>            
        </Group>
	</Panel>
);

Items.propTypes = {
	id: PropTypes.string.isRequired,
    go: PropTypes.func.isRequired,
};

export default Items;



